import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniPopup
	},
	data(){
		return{
			bankObj:{
				mainBankNum:"",
				bankEnrollName:""
			},
			shopObj:{},
			type:"",// 1 不绑定 2 绑定
			isShow:false,
		}
	},
	methods:{
		isShowChange(){
			this.isShow=!this.isShow
		},
		//解绑
		untying(){
			console.log("解绑")
		},
		//更换银行卡
		change(obj){
			uni.navigateTo({
				url:"/pages/capital/changeBank?userName="+obj
			})
		}
	},
	onLoad(e){
		var that=this;
		that.type=e.type;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
	},
	onShow(){
		let that=this;
		that.$ajax({
			url:"/fund/searchBankCard",
			data:{
				marchantId:that.shopObj.merchantId
			},success(d){
				that.bankObj=d;
			}
		})
	}
	
}
